import React from 'react'

import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'

import SEO from '../seo'

import LinkButton from '../link-button'

import { StyledLayout } from './new-layout'

const styles = theme => ({
  // TODO: 使用统一色系的不同深度表示数量
  chip: {
    marginRight: theme.spacing(2),
  },
  banner: {
    backgroundColor: theme.palette.primary.dark,
    paddingTop: 40,
    paddingBottom: 60,
  },
  post: {
    marginTop: -20,
    padding: 20,
  },
  title: {
    color: theme.palette.primary.contrastText,
    // marginTop: 20,
  },
  tag: {
    color: theme.palette.primary.contrastText,
    borderColor: theme.palette.primary.contrastText,
    marginRight: 10,
  },
  container: {
    padding: 20,
  },
})

const PostLayout = ({
  children,
  classes,
  pageContext: {
    frontmatter: { date, tags, title, description },
  },
}) => {
  let keyWords = [`Jokcy`, '博客', '前端', 'React']

  keyWords = Array.isArray(tags) ? [...keyWords, ...tags] : [...keyWords, tags]

  const bannerRender = (
    <div className={classes.banner}>
      <Typography variant="h4" gutterBottom className={classes.title}>
        {title}
      </Typography>
      <div>
        {tags.map(tag => (
          <LinkButton
            variant="outlined"
            size="small"
            to={`/tags/${tag}`}
            className={classes.tag}
            key={tag}
          >
            {tag}
          </LinkButton>
        ))}

        <LinkButton to="/tags/__all" className={classes.tag} size="small">
          所有标签
          <KeyboardArrowRight />
        </LinkButton>
      </div>
    </div>
  )

  return (
    <StyledLayout banner={bannerRender}>
      <SEO title={title} keywords={keyWords} description={description} />
      <div className={`markdown-body ${classes.container}`}>{children}</div>
    </StyledLayout>
  )
}

export default withStyles(styles)(PostLayout)
