import React from 'react'

import { makeStyles, styled } from '@material-ui/styles'

import Container from '@material-ui/core/Container'
import Paper from '@material-ui/core/Paper'

import MainBar from './common/main-bar'
import Footer from './common/footer'

const MAX_WIDTH = 'md'

const useStyles = makeStyles(theme => ({
  main: {
    minHeight: 'calc(100vh - 300px)',
    marginBottom: 40,
  },
  banner: {
    backgroundColor: theme.palette.primary.dark,
  },
  '@global': {
    a: {
      'background-color': 'transparent',
      color: 'inherit',
      textDecoration: 'none',
      '-webkit-text-decoration-skip': 'objects',
      '&:active, &:hover': {
        'outline-width': 0,
      },
    },
  },
}))

const useContainerPageStyle = makeStyles({
  paper: {
    marginTop: -20,
  },
})

const PaddingContainer = styled(Container)({})

function ContainerPaper({ children, ...props }) {
  const classes = useContainerPageStyle()

  return (
    <Container {...props} className={classes.paper}>
      <Paper>{children}</Paper>
    </Container>
  )
}

const Layout = ({ children, banner }) => {
  const classes = useStyles()

  return (
    <>
      <MainBar maxWidth={MAX_WIDTH} />

      <div className={classes.banner}>{banner}</div>

      <div className={classes.main}>{children}</div>

      <Footer maxWidth={MAX_WIDTH} />
    </>
  )
}

const BannerContent = function({ children }) {
  return <PaddingContainer maxWidth={MAX_WIDTH}>{children}</PaddingContainer>
}

const StyledLayout = function({ banner, children, ...rest }) {
  const bannerContent = <BannerContent>{banner}</BannerContent>

  return (
    <Layout banner={bannerContent} {...rest}>
      <ContainerPaper maxWidth={MAX_WIDTH}>{children}</ContainerPaper>
    </Layout>
  )
}

export default Layout

export { StyledLayout, BannerContent }
