// You can delete this file if you're not using it
import React from 'react'

import TopLayout from './src/components/layout/top-layout'
import PostLayout from './src/components/layout/post-layout'
import BlogTagsLayout from './src/components/layout/blog-tag-layout'

import 'github-markdown-css'

export const wrapPageElement = props => {
  return (
    <TopLayout>
      <App {...props} />
    </TopLayout>
  )
}

const App = ({ element, props }) => {
  const pathname = props.location.pathname

  if (pathname.startsWith('/tags')) {
    return <BlogTagsLayout {...props}>{element}</BlogTagsLayout>
  }

  if (pathname.startsWith('/blog')) {
    return <PostLayout {...props}>{element}</PostLayout>
  }

  return element
}
