import React, { useState, useCallback } from 'react'

import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import { makeStyles, styled } from '@material-ui/styles'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'

import Container from '@material-ui/core/Container'

import Sign from '../../images/Sign'

const useStyles = makeStyles(theme => ({
  root: {
    background: theme.palette.primary.dark,
    minHeight: 250,
  },
  inner: {
    display: 'flex',
    alignItems: 'flex-start',
    justifyContent: 'space-between',
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column',
      alignItems: 'center',
    },
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
  },
  paper: {
    // backgroundColor: theme.palette.secondary.dark,
    width: 300,
    marginTop: -20,
    padding: 20,
    [theme.breakpoints.down('sm')]: {
      width: '100%',
    },
  },
  input: {
    marginBottom: 30,
  },
  formTitle: {
    marginBottom: 15,
  },
  bottomTitle: {
    color: '#fff',
  },
  right: {
    display: 'flex',
    flexDirection: 'column',
    height: 150,
    justifyContent: 'space-between',
    alignItems: 'flex-end',
    [theme.breakpoints.down('sm')]: {
      height: 100,
      paddingBottom: 20,
    },
  },
  projects: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-start',
    paddingTop: 20,
    paddingRight: 20,
    [theme.breakpoints.down('sm')]: {
      width: '100%',
      flexDirection: 'row',
      alignItems: 'center',
      justifyContent: 'flex-end',
    },
  },
  projectsTitle: {
    color: '#fff',
    marginBottom: 20,
    [theme.breakpoints.down('sm')]: {
      display: 'none',
    },
  },
  projectButton: {
    display: 'flex',
    alignItems: 'flex-end',
  },
  sign: {
    width: 200,
  },
}))

const OpenLink = styled(Button)(theme => ({
  color: '#fff',
}))

function Footer({ maxWidth }) {
  const classes = useStyles()

  const [email, setEmail] = useState('')
  const [errMsg, setErrMsg] = useState('')

  function handleSubmit(e) {
    if (!email) {
      setErrMsg('请输入邮箱')
      e.preventDefault()
    } else {
      setErrMsg('')
      window.open('https://buttondown.email/Jokcy', 'popupwindow')
    }
  }

  const handleEmailChange = useCallback(e => {
    setEmail(e.target.value)
  }, [])

  return (
    <div className={classes.root}>
      <Container className={classes.inner} maxWidth={maxWidth}>
        <Paper className={classes.paper}>
          <form
            action="https://buttondown.email/api/emails/embed-subscribe/Jokcy"
            method="post"
            target="popupwindow"
            className={classes.form}
            onSubmit={handleSubmit}
          >
            <Typography variant="h6" className={classes.formTitle}>
              订阅技术周报
            </Typography>
            <input type="hidden" value="1" name="embed" />
            <TextField
              error={!!errMsg}
              type="email"
              label="输入你的邮箱"
              name="email"
              value={email}
              onChange={handleEmailChange}
              className={classes.input}
            />
            <Button type="submit" variant="contained" color="secondary">
              订阅我
            </Button>
          </form>
        </Paper>
        <div className={classes.right}>
          <div className={classes.projects}>
            <OpenLink
              href="https://github.com/Jokcy/mdx-ppt"
              target="_blank"
              rel="noreffer"
            >
              mdx-ppt
            </OpenLink>
            <OpenLink
              href="https://github.com/Jokcy/seq"
              target="_blank"
              rel="noreffer"
            >
              seq
            </OpenLink>
          </div>
          <Sign className={classes.sign} />
        </div>
      </Container>
    </div>
  )
}

export default Footer
