import React from 'react'
import { navigate, Link } from 'gatsby'

import { withStyles } from '@material-ui/styles'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Typography from '@material-ui/core/Typography'
import Container from '@material-ui/core/Container'

import SEO from '../seo'

import Layout, { BannerContent } from './new-layout'

const styles = theme => ({
  main: {
    marginTop: 20,
  },
  tab: {
    color: theme.palette.primary.contrastText,
  },
  tabs: {
    marginBottom: 20,
    position: 'relative',
    bottom: -2,
  },
  title: {
    paddingTop: 40,
    marginBottom: 20,
    color: theme.palette.primary.contrastText,
  },
})

function ContainerPaper({ children, ...props }) {
  return (
    <Container maxWidth="md" {...props} style={{ marginTop: 20 }}>
      {children}
    </Container>
  )
}

const StyledLayout = function({ banner, children, ...rest }) {
  const bannerContent = <BannerContent>{banner}</BannerContent>

  return (
    <Layout banner={bannerContent} {...rest}>
      <ContainerPaper>{children}</ContainerPaper>
    </Layout>
  )
}

const preventDefault = e => e.preventDefault()

const PostLayout = ({
  data,
  children,
  classes,
  pageContext: { allTags, tag },
}) => {
  function handleTabChange(event, tab) {
    // console.log(tab)
    // 主动跳转
    navigate(encodeURI(`/tags/${tab}`))
  }

  const bannerContent = (
    <>
      <Typography variant="h4" className={classes.title}>
        根据标签进行删选
      </Typography>
      <Tabs value={tag.key} className={classes.tabs} onChange={handleTabChange}>
        {allTags.map(t => (
          <Tab
            className={classes.tab}
            value={t.key}
            key={t.key}
            label={
              <Link to={`/tags/${t.key}`} onClick={preventDefault}>
                {t.key === '__all' ? '所有' : t.key}
              </Link>
            }
          />
        ))}
      </Tabs>
    </>
  )

  return (
    <StyledLayout banner={bannerContent}>
      <SEO
        title={`所有博客-删选：${tag.key}`}
        keywords={allTags.map(t => t.key)}
      />
      <div className={classes.main}>{children}</div>
    </StyledLayout>
  )

  // return (
  //   <LayoutWithBanner bannerRender={bannerContent}>
  //     <Container render={<div className={classes.main} />}>
  //       <Tabs
  //         value={tag.key}
  //         className={classes.tabs}
  //         onChange={handleTabChange}
  //       >
  //         {allTags.map(t => (
  //           <Tab
  //             className={classes.tab}
  //             value={t.key}
  //             key={t.key}
  //             label={
  //               <Link to={`/tags/${t.key}`} onClick={preventDefault}>
  //                 {t.key === '__all' ? '所有' : t.key}
  //               </Link>
  //             }
  //           />
  //         ))}
  //       </Tabs>
  //       {children}
  //     </Container>
  //   </LayoutWithBanner>
  // )
}

export default withStyles(styles)(PostLayout)
